# <script lang="coffee">
debug = require("debug")("modal")
module.exports=
  props:
    show:
      type:Boolean
      twoWay:true
  data:->
    height:0 #modal的高度。用于居中显示
    dimmer:null # dimmer的jquery对象
    modal:null #modal的jquery对象
  ready:->
    # 已经尽可能地优化性能。
    @modal = $(@$el.querySelector(".ui.modal"))
    @dimmer = $(@$el.querySelector(".ui.dimmer"))
    @dimmer.dimmer
      closable:false
    if @show
      @showDimmer()
  computed:
    marginTop:->
      debug("marginTop:",-(@height/2))
      return -(@height/2) #计算modal框的高度，让modal框居中。
  methods:
    showDimmer:->
      @show=true
      debug('watch:显示弹窗')
      @dimmer.dimmer('toggle') #一定要先显示dimmer，再计算高度。否则无法计算出正确的高度
      $("body").addClass("dimmable dimmed")
      @height = @modal.outerHeight()
      if @height >= $(window).innerHeight() #如果modal框高出了窗口的高度，则设置可以滚动
        @modal.addClass("scrolling")
      else
        @modal.removeClass("scrolling")
    hideDimmer:->
      @show=false
      @dimmer.dimmer('hide')
      $("body").removeClass("dimmed") #在切换路由的时候也要移除body的dimmed，否则body无法滚动。
  watch:
    show:(val,oldVal)->
      if val
        @showDimmer()
      else
        @hideDimmer()
# </script>



