# <script lang="coffee">
module.exports=
  props:['conv','onFocus']
  data:->
    message:"" #输入框的内容
    inputting:false
  computed:
    isEmpty:->
      return @message == ""
    isConnected:->#是否已连接上服务器
      return @conv?
  methods:
    onInputFocus:()->
      @inputting = true
      @onFocus()
    onInputBlur:()->
      setTimeout(=> #延迟一下，避免切换太快，无法点击。
        if not $(@$el.querySelector('.main.input.item input')).is(":focus")
          # 如果检查到文本框并没有聚焦，则设置输入状态为false
          @inputting=false
      ,500)
  vuex:
    actions:
      sendMessage:({state,dispatch})->
        console.log "发送消息",@message
        @inputting = true
        @$el.querySelector('.main.input.item input').focus()
        if @message!="" and @conv? #消息不能为空，并且已经连接上服务器
          msg = {text:@message}
          messageLog = #构造一条聊天记录。
            fromPeerId:state.accounts.currentUser?.id
            msg:msg
            timestamp:Date.now()
            sending:true
          dispatch('ADD_CONV_LOG',@conv.id,messageLog) #添加聊天记录
          @conv.send msg,(data)->
            console.log "消息发送成功",data
            messageLog.sending = false
          @message = ""
# </script>



