# <script lang="coffee">
actions = require "../../vuex/actions.coffee"
toastr = require "toastr"
debug = require("debug")("stronghold")
module.exports=
  data:->
    stronghold:null
    conv:null
    showIndustryModal:false #是否显示工业窗口
    showBuildingModal:false #是否显示建筑窗口
  components:
    SimpleMap:require "./simpleMap.vue"
    ChatMenu:require "./chatMenu.vue"
    ChatBubble:require "./chatBubble.vue"
    SideTabs:require "./sideTabs.vue"
    ItemsModal:require "../../components/items/itemsModal.vue"
    Navbar:require "../../components/navbar.vue"
    IndustryModal:require "./industryModal.vue"
    BuildingModal:require "./buildingModal.vue"
  computed:
    strongholdId:->
      @$route.params.id
    convId:->
      @stronghold?.get('conv').id
    convLogs:->
      @convLogsById?[@convId]
    location:->
      @stronghold?.get('location')
    convMembers:->
      @convMembersById[@convId]
    bgImageUrl:->
      @stronghold?.get('bgImage').url()
    strongholdName:->
      @stronghold?.get('name')
  methods:
    scrollToBottom:->
      if $(window).innerWidth() > 991 #如果是桌面端的设备
        el = $('.ui.chat.list')
      else
        el = $('body') #如果是移动端
      # (使用body,html是因为兼容firefox。)详细：http://stackoverflow.com/questions/8149155/animate-scrolltop-not-working-in-firefox
      setTimeout(->
        el.animate {scrollTop: el.prop("scrollHeight")-el.innerHeight()},'fast'
      ,200)
  ready:->
    $('.ui.dropdown').dropdown
      action: 'hide'
  watch:
    convLogs:(val,oldVal)->
      height = $(document).height() #整个页面的高度
      distance = height-($(document).scrollTop()+$(window).height())
      if distance<500 # 到达底部的距离
        @scrollToBottom()
  route:
    activate:(transition)->
      if @realtimeFlags.isOpen==false #实时通信未开启
        toastr.info "正在连接到服务器……"
        @realtimeObj.once "open",->
          toastr.clear()
          toastr.success("已成功建立连接")
          transition.next()
      else
        transition.next()
      return
    data:(transition)->
      strongholdId = transition.to.params.id
      stronghold = @$store.state.strongholds.strongholdById[strongholdId]
      if stronghold?
        debug "使用缓存的地点。"
        @$store.dispatch("SET_NAV_TITLE",stronghold.get('name'))
        conv = @joinConv(stronghold.get('conv').id) #加入当前据点的聊天暂态对话
        transition.next({stronghold,conv})
        return
      query = new AV.Query("Stronghold")
      query.get(strongholdId).then((stronghold)=>
        @$store.dispatch("SET_NAV_TITLE",stronghold.get('name'))
        @$store.dispatch("CACHE_STRONGHOLDS",[stronghold]) #缓存当前的数据。
        conv = @joinConv(stronghold.get('conv').id) #加入当前据点的聊天暂态对话
        transition.next({stronghold,conv})
      ,(error)->
        transition.abort(error)
      )
      return
  vuex:
    getters:
      realtimeFlags:(state)->
        state.realtime.flags
      realtimeObj:(state)->
        state.realtime.realtimeObj
      convLogsById:(state)->
        state.realtime.convLogsById
      convMembersById:(state)->
        state.realtime.convMembersById
    actions:
      fetchUserAndAdd:({state,dispatch},userId,convId)->
        #根据消息取回用户，并且添加到对应对话中。
        user = AV.Object.createWithoutData('_User',userId)
        dispatch("SET_USER_HASH",user)
        user.fetch().then((user)->
          try
            debug "查找用户成功！",user
            dispatch("ADD_CONV_MENBER",convId,user)
          catch error
            console.error error
        ,(error)->
          debug "fetch用户信息失败。",error
        )
      joinConv:({state,dispatch},convId)->
        debug "尝试加入聊天",convId
        conv = state.realtime.convById[convId]
        if conv? #如果聊天对象已经缓存，则直接返回。
          debug "conv已缓存。"
          return conv
        #否则根据id获取对象。
        conversationObj = @realtimeObj.conv convId,(conv)=>
          debug "查找conv成功"
          if conv?
            debug "conv:",conv
            dispatch("SET_CONV_HASH",conv)
            conv.join((data)=>
              debug "加入聊天成功！",data
            )
            conv.log({limit:20},(messages)=>
              dispatch("ADD_CONV_LOGS",convId,messages)
              for message in messages
                if not state.accounts.usersById[message.fromPeerId]?
                  #如果用户不存在，则
                  @fetchUserAndAdd(message.fromPeerId,conv.id) #取回用户
              setTimeout(=>
                @scrollToBottom()
              ,500)
              return
            )
            return conv
          else
            console.error "找不到指定的conv对象。无法加入聊天。convId：#{convId}"
# </script>



